<template>
  <div>
    <div v-if="userProfile !== null">
      <div class="row">
        Logado como {{userProfile.name}} <a class="waves-effect waves-light btn right " v-on:click="logout">Log out</a>
      </div>
    </div>
    <div v-else>
      <div v-if="openForm">
        <div class="row">
          <a class="waves-effect red waves-light btn right" v-on:click="cancel">X</a>
          <div class="col s12 m4 offset-m4">
            <div id="classicLogin">
              <h4 class="center">Iniciar sesión en QuoTravel</h4>
                <div class="row">
                  <form class="col offset-s1 s10 ">
                    <div class="row">
                      <div class="input-field col s12">
                        <input v-bind=user id="user" type="text" class="validate">
                        <label for="user">User</label>
                      </div>
                    </div>
                    <div class="row">
                      <div class="input-field col s12">
                        <input v-bind=password id="password" type="password" class="validate" >
                        <label for="password">Password</label>
                      </div>
                    </div>
                    <div class="row">
                      <div class="col s12 waves-effect waves-light btn" v-on:click="login">Log in
                      </div>
                    </div>
                    <div class="row">
                      <div class="col s12 waves-effect waves-light btn" v-on:click="register">Sign in
                      </div>
                    </div>
                    <div v-if="providers" id="oauth">
                      <div class="row center"><h4> Or log with ...</h4></div>
                      <div class="row" v-for="provider in providers">
                        <div :class="'btn_social ' + provider.name " @click="authenticate(provider.name)">Login With {{provider.name}}</div>
                      </div>
                    </div>

                  </form>
                </div>
            </div>
          </div>
        </div>
      </div>
      <div v-else>
        <div class="row">
          <a class="waves-effect waves-light btn  right" v-on:click="{{openformdata()}}">Login</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'Login',
  data: function () {
    return {
      user: null,
      password: null,
      openForm: null,
      response: null,
      providers: null,
      userProfile: globaldata.userprofile

    }
  },
  mounted () {
    var d = this.$data
    d.openForm = null
    this.$emit('clicked', d.openForm)

    function getProviders () {
      return axios.post('http://localhost:4000/providers').then(response => {
        return response.data
      })
    }

    getProviders().then(data => {
      d.providers = data
      for (var provider1 in d.providers) {
        for (var provider2 in this.$auth.options.providers) {
          if (provider1 === provider2) {
            this.$auth.options.providers[provider2].clientId = d.providers[provider1].clientId
            this.$auth.options.providers[provider2].clientSecret = d.providers[provider1].clientSecret
            this.$auth.options.providers[provider2].redirectUri = d.providers[provider1].redirectUri
          }
        }
      }
    })
  },
  methods: {

    login: function () {
      var t = this
      var d = this.$data
      let user = {
        email: d.user,
        password: d.password // encriptar
      }

      if (this.$auth.isAuthenticated()) {
        this.$auth.logout()
      }

      this.$auth.login(user).then(function (response) {

        if (response.status === 200) {
          d.response = response
          d.errorLogin = null

          globaldata.userprofile = response.data
          d.userProfile = globaldata.userprofile
          sessionStorage.setItem('userprofile' , JSON.stringify(globaldata.userprofile))

          t.$emit('clicked', false)

        } else {
          d.userProfile = null
          sessionStorage.clear()
          globaldata.userprofile = null
          d.errorLogin = 'error al logarse intentelo de nuevo'
        }
      })
    },

    register: function () {
      // this.$auth.register({ name, user, password }).then(function () {
      // Execute application logic after successful registration
      // })
      this.$router.push({ name: 'welcome' })
    },

    // logout limpia variable de entorno
    logout: function () {
      if (this.$auth.isAuthenticated()) {
        this.$auth.logout()
      }
      var d = this.$data
      d.userProfile = null
      sessionStorage.clear()
      globaldata.userprofile = null
      d.openForm = null
      this.$emit('clicked', d.openForm)
    },

    cancel: function () {
      if (this.$auth.isAuthenticated()) {
        this.$auth.logout()
      }
      var d = this.$data
      d.userProfile = null
      sessionStorage.clear()
      globaldata.userprofile = null
      d.openForm = null
      this.$emit('clicked', d.openForm)
    },

    // autenticacion contra los diferentes oauth
    authenticate: function (provider) {
      var d = this.$data
      if (this.$auth.isAuthenticated()) {
        this.$auth.logout()
      }
      this.response = null
      this.$auth.authenticate(provider).then(function (authResponse) {
        d.response = authResponse
        d.logedin = true
      })
        .catch(function (error) {
          console.log('Incorrecto ' + error)
        })
    },
    openformdata: function () {
      var d = this.$data
      d.openForm = true
      this.$emit('clicked', d.openForm)
    }
  }
}
</script>

<style >
  .btn_social {

    padding: 6px 12px;
    font-size: 14px;
    font-weight: normal;
    line-height: 1.42857143;
    vertical-align: middle;
    -ms-touch-action: manipulation;
    touch-action: manipulation;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    background-image: none;
    border: 1px solid transparent;
    border-radius: 4px;
    position: relative;
    text-align: left;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

    margin-bottom: 10px;
    color: #ffffff;
  }
  .github{
    background-image: url(/static/images/buttons/github.png); /* 16px x 16px */
    background-size:contain;
    background-color: #2b414d; /* make the button transparent */
    background-repeat: no-repeat;  /* make the background image appear only once */
  }
  .facebook{
    background-image: url(/static/images/buttons/facebook.png); /* 16px x 16px */
    background-size:contain;
    background-color: #47479C; /* make the button transparent */
    background-repeat: no-repeat;  /* make the background image appear only once */
  }
  .google{
    background-image: url(/static/images/buttons/google.png); /* 16px x 16px */
    background-size:contain;
    background-color: #d34836; /* make the button transparent */
    background-repeat: no-repeat;  /* make the background image appear only once */
  }
  .boton_personalizado:hover{
    color: #1883ba;
    background-color: #ffffff;
  }

</style>



