import Vue from 'vue'
import VueAxios from 'vue-axios'
import VueAuthenticate from 'vue-authenticate'
import axios from 'axios'
import Router from 'vue-router'
import Reserva from '@/components/commons/Reserva'
import Ok from '@/components/commons/Ok'
import Ko from '@/components/commons/Ko'
import Config from '@/components/commons/Config'
import Login from '@/components/commons/Login'
import Welcome from '@/components/commons/Welcome'
import Pago from '@/components/commons/Pago'
import HotelFormulario from '@/components/hotel/Formulario'
import HotelDispo from '@/components/hotel/Dispo'
import HotelRates from '@/components/hotel/Rates'
import HotelDetalle from '@/components/hotel/Detalle'

Vue.use(Router)
Vue.use(VueAxios, axios)
var authenticateData = {
  tokenName: 'access_token',
  baseUrl: 'http://localhost:4000',
  storageType: 'localStorage'
}

Vue.use(VueAuthenticate, authenticateData)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'reserva',
      component: Reserva,
      children: [
        {
          path: '',
          name: 'hotel_formulario',
          component: HotelFormulario
        },

        {
          path: 'dispo',
          name: 'hotel_dispo',
          component: HotelDispo
        },
        {
          path: 'rates',
          name: 'hotel_rates',
          component: HotelRates
        },
        {
          path: 'detalle',
          name: 'hotel_detalle',
          component: HotelDetalle
        },
        {
          path: 'ok',
          name: 'ok',
          component: Ok
        },
        {
          path: 'ko',
          name: 'ko',
          component: Ko
        },
        {
          path: 'config',
          name: 'config',
          component: Config
        },
        {
          path: 'login',
          name: 'login',
          component: Login
        },
        {
          path: 'welcome',
          name: 'welcome',
          component: Welcome
        },
        {
          path: 'pago',
          name: 'pago',
          component: Pago
        }
      ]
    }

  ]
})



