<template>
  <div id="app-logic">
    <Login @clicked="onclicklogin"></Login>
    <div v-show="openForm !== true">
      <router-view/>
    </div>
  </div>
</template>

<script>
import Login from './Login';

export default {
  name: 'Reserva',
  components: {Login},
  data: function () {
    return {
      openForm: null
    }
  },
  methods: {
    onclicklogin (value) {
      var d = this.$data
      d.openForm = value
    }
  }
}
</script>

<style scoped>

</style>



