<template>
<div class="container center">
  <h1>Thank you!</h1>
  <i class="material-icons green large circle">done</i>
  <h4>Your booking has been confirmed with ID: {{bookingId}}</h4>
  <div class="input-field inline">
    <a class="waves-effect waves-light btn" :src="baseurl" >Añadir servicios</a>
  </div>
  <div class="input-field inline">
    <a class="waves-effect waves-light btn" v-on:click="pago()">Realizar Pago</a>
  </div>
</div>
</template>

<script>
var  globalbaseurl = globaldata.baseurl

export default {
  name: 'Ok',
  data: function () {
    return {
      bookingId: null,
      baseurl: globalbaseurl
    }
  },
  mounted () {
    this.$data.bookingId = this.$route.query.bookingId
  },
  methods: {
    pago: function (e) {
      //e.preventDefault()
      this.$router.push({name: 'pago'})
    }
  }
}
</script>

<style scoped>

</style>



